@import url(https://fonts.googleapis.com/css?family=Hind&display=swap);
@import url(https://fonts.googleapis.com/css?family=Staatliches&display=swap);
.search-list{font-family:'Hind', sans-serif;background-color:white;text-align:center}.search-list p{padding-top:20px;margin-left:30px;font-size:24px}.search-list ul{margin:10px}.search-list li{list-style-type:none;padding:10px}

.Navbar ul li{list-style-type:none;display:inline;margin:0 3em;border-radius:1px;padding:1em 2em}.Navbar ul li a{color:black;font-family:Arial, Helvetica, sans-serif}

body{display:flex;flex-direction:column;align-items:center;background-color:#dbf1f1;font-family:'Staatliches', cursive;width:100%}.App{flex-direction:column}form .text-input{width:250px;height:20px;margin:40px 20px 20px 20px;font-size:15px}form button{width:100px;height:30px;font-size:20px;margin:10px;height:40px;font-family:'Staatliches', cursive;background:#fffdfd}form label{font-size:22px;margin-left:50px}.fetch-error{text-align:center;color:red}.loading{margin-left:225px;margin-top:50px}

header{text-align:center;font-size:30px}

.results{background-color:white;padding:35px 50px;margin-top:20px}

footer{margin-top:16em;text-align:center}

